body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

body{
    margin: 0;
    background-image: url("/Background.jpg");
    background-size: 100%;
    background-repeat: unset;
}
.header{
    height: 14vh;
    background-color: rgba(
        31, 32, 33
        ,0.5);
    border-bottom-left-radius: 5mm;
    border-bottom-right-radius: 5mm;
    display: flex;
    flex-direction: row;
    justify-content: space-around;
    align-items: center;
    overflow: hidden;
}

.header #logo{
    height: 70%
}
.header #darth{
    position: relative;
    height: 100%;
    -webkit-transform: scale(2);
            transform: scale(2);
    top: 50%;
}
.header #yoda{
    position: relative;
    height: 100%;
    -webkit-transform: scale(2);
            transform: scale(2);
    top: 50%;
}

.schema{
    margin-left: 10%;
    margin-right: 10%;
}
th,td{
    color: white;
}
table {
    border-spacing: 1rem;
    letter-spacing: 0.2rem;
  }


.Pagination
{
    /* background-color: red; */
    display: flex;
    margin-top: 5%;
    width: 50em;
    -webkit-transition: 0.3s;
    transition: 0.3s;
}
.Pallete{
    width: 43em;
    /* margin: auto; */
    border: 2px solid #e35252;
    margin-top: 10vh;
    border-radius: 10px;
    overflow: hidden;
}
.Arrows{
    display: flex;
    align-items: center;
}
.Parent{
    display: flex;
    align-items: center; 
    justify-content: space-around;
}
#left .large{
    color: white;
    cursor: pointer;
    -webkit-animation: moveLeft 1s infinite ease-out;
            animation: moveLeft 1s infinite ease-out;
    -webkit-transition-duration: 0.5ms;
            transition-duration: 0.5ms;
}
#left .small{
    color: white;
    -webkit-transform: scale(1);
            transform: scale(1);
    cursor: pointer;
    -webkit-animation: moveLeft 1s infinite ease-out;
            animation: moveLeft 1s infinite ease-out;
    -webkit-transition-duration: 0.5ms;
            transition-duration: 0.5ms;
}
#right .large{
    color: white;
    cursor: pointer;
    -webkit-animation: moveRight 1s infinite ease-out;
            animation: moveRight 1s infinite ease-out;
    -webkit-transition-duration: 0.5ms;
            transition-duration: 0.5ms;
}
#right .small{
    color: white;
    -webkit-transform: scale(1);
            transform: scale(1);
    cursor: pointer;
    -webkit-animation: moveRight 1s infinite ease-out;
            animation: moveRight 1s infinite ease-out;
    -webkit-transition-duration: 0.5ms;
            transition-duration: 0.5ms;
}
@-webkit-keyframes moveLeft{
    0%{
        -webkit-transform: scale(2.5);
                transform: scale(2.5);
        -webkit-transform: translateX(0px);
                transform: translateX(0px);
    }
    50%{
        -webkit-transform: scale(2.5);
                transform: scale(2.5);
        -webkit-transform: translateX(-15px);
                transform: translateX(-15px);
    }
   100%{
    -webkit-transform: scale(2.5);
            transform: scale(2.5);
    -webkit-transform: translateX(0px);
            transform: translateX(0px);
   }
}
@keyframes moveLeft{
    0%{
        -webkit-transform: scale(2.5);
                transform: scale(2.5);
        -webkit-transform: translateX(0px);
                transform: translateX(0px);
    }
    50%{
        -webkit-transform: scale(2.5);
                transform: scale(2.5);
        -webkit-transform: translateX(-15px);
                transform: translateX(-15px);
    }
   100%{
    -webkit-transform: scale(2.5);
            transform: scale(2.5);
    -webkit-transform: translateX(0px);
            transform: translateX(0px);
   }
}
@-webkit-keyframes moveRight{
    0%{
        -webkit-transform: scale(2.5);
                transform: scale(2.5);
        -webkit-transform: translateX(0px);
                transform: translateX(0px);
    }
    50%{
        -webkit-transform: scale(2.5);
                transform: scale(2.5);
        -webkit-transform: translateX(15px);
                transform: translateX(15px);
    }
   100%{
    -webkit-transform: scale(2.5);
            transform: scale(2.5);
    -webkit-transform: translateX(0px);
            transform: translateX(0px);
   }
}
@keyframes moveRight{
    0%{
        -webkit-transform: scale(2.5);
                transform: scale(2.5);
        -webkit-transform: translateX(0px);
                transform: translateX(0px);
    }
    50%{
        -webkit-transform: scale(2.5);
                transform: scale(2.5);
        -webkit-transform: translateX(15px);
                transform: translateX(15px);
    }
   100%{
    -webkit-transform: scale(2.5);
            transform: scale(2.5);
    -webkit-transform: translateX(0px);
            transform: translateX(0px);
   }
}
.loadingRings{
     z-index: 5;
     margin: auto;
     margin-top: 20%;
     height: 10vh;
     width: 2em;
     
}
.loadingRings #circle1{
    color: white;
    position: absolute;
    -webkit-transform: scale(0);
            transform: scale(0);
    -webkit-animation: ripple 1.5s infinite;
            animation: ripple 1.5s infinite;
}
.loadingRings #circle2{
    color: white;
    position: absolute;
    -webkit-transform: scale(0);
            transform: scale(0);
    -webkit-animation: ripple 1.5s infinite;
            animation: ripple 1.5s infinite;
    -webkit-animation-delay: 0.5s;
            animation-delay: 0.5s;
}

@-webkit-keyframes ripple{
    0%{
        -webkit-transform: scale(0);
                transform: scale(0);
        opacity: 1;
    }
    95%{
        -webkit-transform: scale(8);
                transform: scale(8);
        opacity: 0;
    }
    100%{
        -webkit-transform: scale(0);
                transform: scale(0);
        opacity: 0;
    }
}

@keyframes ripple{
    0%{
        -webkit-transform: scale(0);
                transform: scale(0);
        opacity: 1;
    }
    95%{
        -webkit-transform: scale(8);
                transform: scale(8);
        opacity: 0;
    }
    100%{
        -webkit-transform: scale(0);
                transform: scale(0);
        opacity: 0;
    }
}
.loadingRings span{
    position: absolute;
    color: white;
    -webkit-transform: translate(-45%,70px);
            transform: translate(-45%,70px);
    font-size: 2em;
    font-family: monospace;
}

